@font-face {
  font-family: inter;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: inter;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: inter;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: inter;
  src: url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: franklin;
  src: url("../fonts/Franklin-Gothic-Heavy.woff2") format("woff2"), url("../fonts/Franklin-Gothic-Heavy.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: druk;
  src: url("../fonts/Druk-Text-Wide-Medium.woff2") format("woff2"), url("../fonts/Druk-Text-Wide-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
:root {
  --color-primary: #002760;
  --color-title: black;
  --color-text: rgba(0, 0, 0, 0.75);
  --color-secondary: rgb(19, 50, 62);
  --color-contrast: rgba(175, 150, 135, 1);
  --color-brown: #cb9e88;
  --color-dark: #1b1919;
  --color-light: #fff2e9;
  --color-salmon: #e9856f;
  --color-cornflowerblue: #6f85e9;
  --bg-color-primary: rgba(240, 240, 240, 0.3);
  --bg-color-secondary: #fff2e9;
  --main-font-regular: normal normal 400 1rem "inter", sans-serif;
  --main-font-medium: normal normal 500 1rem "inter", sans-serif;
  --main-font-semi-bold: normal normal 600 1rem "inter", sans-serif;
  --main-font-bold: normal normal 700 1rem "inter", sans-serif;
  --secondary-font-bold: normal normal 700 1rem "franklin", sans-serif;
  --ternary-font-medium: normal normal 500 1rem "druk", sans-serif;
  --main-bg-gradient: linear-gradient(90deg, rgba(135, 150, 175, 1) 0%, rgba(255, 255, 255, 0) 100%);
  --header-height: 4rem;
  --text-shadow-primary: 1px 1px 2px rgba(0, 0, 0, 0.5) ;
}
html,
body {
  box-sizing: border-box;
  margin: 0;
  font: var(--main-font-regular);
  /* background: var(--main-bg-gradient) no-repeat; */
  height: 100%;
  color: var(--color-primary);
  background-color: #FFFFFF;
}

.title-main {
  color: var(--color-title);
  font: var(--main-font-bold);
  font-size: 4rem;
  letter-spacing: -1.2px;
  margin: 0;
}
@media (max-width: 430px) {
  .title-main {
    font-size: 2.8rem;
  }
}

.title-secondary {
  color: var(--color-title);
  font: var(--main-font-semi-bold);
  font-size: 3rem;
  letter-spacing: -1.2px;
  margin: 0 0 2.5rem;
}

.page-title {
  text-align: center;
  font: var(--secondary-font-bold);
  font-size: 4rem;
  line-height: 1;
  color: #2a4074;
  line-height: 1.2;
}
@media (max-width: 430px) {
  .page-title {
    font-size: 1.5rem;
  }
}
.page-title_light {
  color: #436490;
}
.page-title_light-alt {
  color: #7e8aa4;
}

.text-main {
  color: var(--color-text);
  font: var(--main-font-regular);
  font-size: 1.5rem;
  margin: 0;
}

.divider {
  box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.5);
}
.content-wrapper {
  margin: auto;
  width: 100%;
  max-width: 1440px;
  box-sizing: border-box;
  position: relative;
  padding: 0 5rem;
}
@media (max-width: 430px) {
  .content-wrapper {
    padding: 0 1rem;
  }
}
.run-string {
  padding: 5rem;
}
.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: var(--header-height);
  position: sticky;
  top: 0;
  z-index: 9;
  font-size: 1.25rem;
  background-color: white;
  margin-bottom: 6.25rem;
}
@media (max-width: 430px) {
  .header {
    margin-bottom: 3rem;
  }
}
.header__inner {
  display: flex;
  justify-content: space-between;
}
.header__menu {
  align-self: flex-end;
  display: flex;
  justify-content: space-between;
  gap: 1.8rem;
  padding-bottom: 0.2rem;
}
@media (max-width: 430px) {
  .header__menu {
    display: none;
  }
}

.menu__link {
  color: var(--color-primary);
  text-decoration: none;
  transition: color 0.2s;
  font: var(--main-font-medium);
}

.menu__link_active {
  color: var(--color-contrast);
}

.menu__link:hover {
  color: var(--color-contrast);
}

.logo {
  background: no-repeat center/contain url(../img/logo.svg);
  height: 2.6875rem;
  width: 12rem;
}
.burger-menu {
  align-self: center;
  display: none;
}
@media (max-width: 430px) {
  .burger-menu {
    display: block;
  }
}

.burger-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 2.5rem;
  height: 2rem;
}

.burger-menu__line {
  background: var(--color-primary);
  height: 0.1rem;
  /* margin: .1rem; */
  width: 100%;
  transition: all 0.3s ease;
}

.burger-btn.active .burger-menu__line:nth-child(1) {
  transform: translateY(5px) rotate(45deg);
}

.burger-btn.active .burger-menu__line:nth-child(2) {
  opacity: 0;
}

.burger-btn.active .burger-menu__line:nth-child(3) {
  transform: translateY(-5px) rotate(-45deg);
}

.burger-menu__layer {
  color: var(--color-primary);
  position: absolute;
  top: calc(var(--header-height) - 0.8rem);
  left: 0;
  right: 0;
  /* bottom: 0; */
  background-color: white;
  /* border: 1px solid #ddd; */
  /* width: 200px; */
  overflow: hidden;
  transition: all 0.3s ease;
  height: 0;
  z-index: 2;
  touch-action: none;
  box-sizing: border-box;
}

.burger-menu__layer.active {
  height: 100vh;
}

.menu-item {
  padding: 12px 15px;
  color: var(--color-primary);
  text-decoration: none;
  display: block;
  transition: background-color 0.2s;
}

.menu-item:hover {
  background-color: #f5f5f5;
}

.menu-item:not(:last-child) {
  border-bottom: 1px solid #eee;
}
.detail-button {
  cursor: inherit;
  background-color: black;
  color: white;
  border-radius: 0.5rem;
  padding: 0.875rem 1.5rem;
  font: var(--main-font-medium);
  font-size: 1rem;
}
.home-page__header {
  margin-bottom: 7.5rem;
}
@media (max-width: 430px) {
  .home-page__header {
    margin-bottom: 3rem;
  }
}
.home-page__title {
  margin-bottom: 1.5rem;
}
.home-page__description {
  line-height: 1.5;
  margin-bottom: 5rem;
}
.home-page__description-accented {
  color: var(--color-primary);
}
.home-page__button-wrapper {
  display: flex;
  justify-content: flex-end;
}
.home-page__parallax-block {
  background-image: url(../img/home-screen/home-screen-image.jpg);
  background-attachment: fixed;
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
}
@media (max-width: 430px) {
  .home-page__parallax-block {
    background-image: url(../img/home-screen/home-screen-image-430.jpg);
    background-attachment: local;
  }
}
.last-years {
  padding: 10rem 10rem;
  box-sizing: border-box;
  background-color: var(--bg-color-primary);
}

.last-years__description {
  box-sizing: border-box;
  text-align: center;
  font: var(--secondary-font-bold);
  font-size: 3.2rem;
  letter-spacing: 0.2rem;
  /* color: var(--color-contrast); */
  color: var(--color-primary);
}

@media (min-width: 1024px) and (max-width: 1439px) {
  .last-years {
    padding: 5rem;
  }
  .last-years__description {
    font-size: 2.2rem;
  }
}
@media (max-width: 1023px) {
  .last-years {
    padding: 5vw;
  }
  .last-years__description {
    font-size: 2.2vw;
  }
}
@media (max-width: 414px) {
  .last-years {
    padding: 8vw;
  }
  .last-years__description {
    text-align: center;
    font-size: 4vw;
  }
}

.about-us__title {
  margin: 10rem 0;
}
@media (max-width: 430px) {
  .about-us__title {
    margin: 5rem 0;
  }
}
.about-us__description {
  margin-bottom: 2.5rem;
}
.about-us__photos-wrapper {
  position: relative;
}
.about-us__photo {
  width: 100%;
  margin-bottom: 3rem;
}
.about-us__photo-up {
  position: absolute;
  top: 0;
  left: 0;
}
.about-us__sub-title {
  margin: 10rem 0;
}
@media (max-width: 430px) {
  .about-us__sub-title {
    margin: 5rem 0;
  }
}
.about-us__text {
  color: var(--color-text);
  font: var(--main-font-regular);
  font-size: 1.5rem;
  line-height: 1.5;
  margin-bottom: 2.5rem;
}
.about-us__map {
  margin-bottom: 5rem;
  width: 100%;
}
.contacts-screen__title {
  margin: 10rem 0;
}
@media (max-width: 430px) {
  .contacts-screen__title {
    font-size: 2rem;
    margin: 5rem 0;
  }
}
.contacts-screen__description {
  margin-bottom: 2.25rem;
}
.contacts-screen__logo-wrapper {
  left: 0;
  bottom: 0;
  padding: 3rem 0;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  width: 100%;
}
.contacts-screen__logo {
  width: 20rem;
}
@media (max-width: 430px) {
  .contacts-screen__logo {
    width: 50vw;
    align-self: center;
  }
}
.contacts-screen__contacts-wrapper {
  margin-bottom: 1rem;
}

#map {
  width: 100%;
  height: 500px;
}
.projects__main-title {
  margin-bottom: 4rem;
}
@media (max-width: 430px) {
  .projects__main-title {
    font-size: 2rem;
  }
}
.projects__wrapper {
  display: grid;
  grid-template-columns: 60% 40%;
  grid-template-rows: 1fr 1fr;
  gap: 2.5rem 1rem;
  margin-bottom: 5rem;
}
@media (max-width: 430px) {
  .projects__wrapper {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
}
.projects__wrapper-alternative {
  grid-template-columns: 40% 60%;
}
@media (max-width: 430px) {
  .projects__wrapper-alternative {
    grid-template-columns: 1fr;
  }
}
.projects__wrapper-correct {
  grid-template-rows: 50% 40%;
}
@media (max-width: 430px) {
  .projects__wrapper-correct {
    grid-template-rows: 1fr;
  }
}
.projects__photo {
  border-radius: 0.5rem;
}
@media (max-width: 430px) {
  .projects__photo {
    width: 100%;
  }
}
.projects__title {
  font: var(--main-font-medium);
  font-size: 1.5rem;
  color: var(--color-title);
  margin-bottom: 0.25rem;
}
.projects__address {
  color: var(--color-text);
  font: var(--main-font-regular);
  font-size: 1.25rem;
}
.projects__about {
  color: var(--color-text);
  font: var(--main-font-regular);
  font-size: 1.25rem;
  margin: 0 0 1rem;
  text-align: justify;
  font-size: 1.1rem;
}
.projects__about-ul {
  list-style-type: none;
  padding-left: 1rem;
}
.projects__about-ul li {
  margin-bottom: 1rem;
  text-align: left;
}
.projects__project {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1.5rem;
}
@media (max-width: 430px) {
  .projects__project-correct-order {
    order: -1;
    gap: 0;
  }
}
.projects__project-first {
  grid-row-start: 1;
  grid-row-end: 3;
}
@media (max-width: 430px) {
  .projects__project-first {
    grid-row-start: unset;
    grid-row-end: unset;
  }
}
.projects__project-second {
  grid-row-start: 1;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
}
@media (max-width: 430px) {
  .projects__project-second {
    grid-row-start: unset;
    grid-row-end: unset;
    grid-column-start: unset;
    grid-column-end: unset;
  }
}
.projects__project-third {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
}
@media (max-width: 430px) {
  .projects__project-third {
    grid-row-start: unset;
    grid-row-end: unset;
    grid-column-start: unset;
    grid-column-end: unset;
  }
}
.build-for-people {
  height: 10rem;
  background-image: url(../img/run-strings/build-for-people.png);
  background-repeat: repeat-x;
  background-position-y: center;
  background-position-x: 1000vw;
  animation: 240s run-string-building-for-people forwards infinite linear;
}

@keyframes run-string-building-for-people {
  to {
    background-position-x: -1000vw;
  }
}
.slider__wrapper {
  overflow: hidden;
  margin-bottom: 6.5rem;
}
.slider__lent {
  display: flex;
  gap: 1.5rem;
  transition: transform 0.3s linear;
}
@media (max-width: 430px) {
  .slider__lent {
    display: flex;
    flex-direction: column;
  }
}
.slider__label {
  font: var(--main-font-medium);
  font-size: 1.5rem;
  color: black;
  text-align: center;
}
.command__title {
  margin: 10rem 0;
}
.command__photo {
  margin-bottom: 15rem;
  width: 100%;
}
.command__sub-title {
  font: var(--main-font-medium);
  font-size: 1.5rem;
  color: var(--color-title);
  margin: 0 0 0.5rem;
}
.command__description {
  font: var(--main-font-regular);
  font-size: 1.5rem;
  color: var(--color-text);
  line-height: 1.5;
}
.investo-block-run {
  height: 10rem;
  background-image: url(../img/run-strings/investo.png);
  background-repeat: repeat-x;
  background-position-y: center;
  background-position-x: 1000vw;
  animation: 240s run-string-investo forwards infinite linear;
  margin: 8rem 0 10rem;
}
@media (max-width: 430px) {
  .investo-block-run {
    margin: 4rem 0 5rem;
  }
}

@keyframes run-string-investo {
  to {
    background-position-x: -1000vw;
  }
}
.footer {
  border-top: 1px solid #454545;
  padding: 4.75rem 0;
  display: flex;
  gap: 2rem;
  justify-content: space-between;
}
@media (max-width: 430px) {
  .footer {
    padding: 2rem;
    flex-direction: column-reverse;
  }
}
.footer__lent {
  transition: transform 0.2s linear;
}
.footer__logo {
  width: 20rem;
}
@media (max-width: 430px) {
  .footer__logo {
    width: 50vw;
    align-self: center;
  }
}
.structure__logo-wrapper {
  margin: 10rem 0;
}
.structure__logo {
  margin: auto;
}
.structure__title {
  margin-bottom: 3rem;
}
@media (max-width: 430px) {
  .structure__title {
    margin-bottom: 2rem;
  }
}
.structure__card-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8rem;
  margin-bottom: 2rem;
}
@media (max-width: 430px) {
  .structure__card-wrapper {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}
.structure__card-title {
  color: #204274;
  font: var(--main-font-medium);
  font-size: 1.5rem;
  margin: 0 0 2.1875rem;
}
@media (max-width: 430px) {
  .structure__card-title {
    margin-bottom: 1rem;
  }
}
.structure__card-body {
  color: #828282;
  font: var(--main-font-medium);
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1.5;
  margin: 0;
}
@media (max-width: 430px) {
  .structure__card-body {
    margin: 1rem 0;
  }
}
.contacts__wrapper {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.contacts__title {
  font: var(--main-font-medium);
  font-size: 1rem;
  color: var(--color-title);
  margin: 0;
}
.contacts__text {
  font: var(--main-font-medium);
  color: var(--color-text);
  font-size: 1rem;
  margin: 0;
  text-decoration: none;
}
.counts-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  border-top: 1px solid #dcdcdc;
}
@media (max-width: 430px) {
  .counts-wrapper {
    grid-template-columns: 1fr;
  }
}

.counts-card {
  padding: 2rem;
  font: var(--ternary-font-medium);
}
.counts-card__title {
  font-family: inherit;
  line-height: 1;
  margin: 0 0 5rem;
  color: #585d5d;
  font-weight: 500;
  font-size: 3rem;
}
@media (max-width: 430px) {
  .counts-card__title {
    margin: 0 0 2rem;
  }
}
.counts-card__sub-title {
  font-family: inherit;
  color: #9c9c9c;
  font-weight: 500;
  font-size: 1.5rem;
}
.counts-card__left {
  padding-left: 25%;
}
@media (max-width: 430px) {
  .counts-card__left {
    padding-left: 2rem;
  }
}
.counts-card__center {
  border-left: 1px solid #dcdcdc;
  border-right: 1px solid #dcdcdc;
}
.skyscrapers-images__title {
  margin: 10rem 0 5rem;
}
.skyscrapers-images__images-template {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.skyscrapers-images__photos-wrapper {
  position: relative;
}
.skyscrapers-images__photo {
  width: 100%;
  height: auto;
}
.skyscrapers-images__photo-up {
  position: absolute;
  top: 0;
  left: 0;
}
@keyframes wrapper-animation {
  to {
    opacity: 0;
    display: none;
  }
}
@keyframes logo-animation {
  to {
    transform: scale(0);
  }
}
.preloader {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: white;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}
.preloader_animated {
  animation: 1s ease-in-out forwards 1s wrapper-animation;
}
.preloader__logo {
  transform: scale(1.5);
}
.preloader__logo_animated {
  animation: 1s ease-in forwards 1s logo-animation;
}
@keyframes to-up-animation {
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.to-up-animation-init-position {
  transform: translateY(5rem);
  opacity: 0;
}

.to-up-animation {
  animation: 0.5s forwards ease to-up-animation;
}

@keyframes to-scale-animation {
  to {
    transform: scale(5);
    opacity: 1;
  }
}
@media (max-width: 430px) {
  @keyframes to-scale-animation {
    to {
      transform: scale(2);
      opacity: 1;
    }
  }
}
.to-scale-animation-element-init-position {
  transform: scale(3);
  opacity: 0;
}
@media (max-width: 430px) {
  .to-scale-animation-element-init-position {
    transform: scale(1);
  }
}

.to-scale-animation {
  animation: 0.8s forwards ease to-scale-animation;
}