.slider {
  &__wrapper {
    overflow: hidden;
    margin-bottom: 6.5rem;
  }

  &__lent {
    display: flex;
    gap: 1.5rem;
    transition: transform .3s linear;

    @media (max-width: 430px) {
      display: flex;
      flex-direction: column;
    }
  }


  &__label {
    font: var(--main-font-medium);
    font-size: 1.5rem;
    color: black;
    text-align: center;
  }
}
