.command {
  &__title {
    margin: 10rem 0;
  }


  &__photo {
    margin-bottom: 15rem;
    width: 100%;
  }

  &__sub-title {
    font: var(--main-font-medium);
    font-size: 1.5rem;
    color: var(--color-title);
    margin: 0 0 .5rem;
  }

  &__description {
    font: var(--main-font-regular);
    font-size: 1.5rem;
    color: var(--color-text);
    line-height: 1.5;
  }
}
